namespace slib {

    type RequestParam = {
        method: "GET" | "POST" | "PUT" | "DELETE",
        url: string,
        data?: any,
        timeout?: number,
        headMap?: { [key: string]: string },
        onDone?: (data: any, code?: number) => void,
        onError?: (error: any, code?: number) => void,
        onTimeout?: () => void,
        onProgress?: (loaded: number, total: number) => void,
        onUploadProgress?: (loaded: number, total: number) => void
    }

    export class HttpClient {
        constructor() {
        }

        /**
         * http
         */
        public request({ method = "GET",
            url = null,
            data = null,
            timeout = 5000,
            headMap = null,
            onDone = null,
            onError = null,
            onTimeout = null,
            onProgress = null,
            onUploadProgress = null,
        }: RequestParam) {

            let http = new XMLHttpRequest();
            let clearListener = () => {
                http.onreadystatechange =
                    http.onprogress =
                    http.onabort =
                    http.onerror =
                    http.ontimeout = null;

                if (http.upload) {
                    http.upload.onprogress = null;
                }
            }

            http.timeout = timeout
            http.open(method, url, true);

            http.onreadystatechange = (ev) => {
                // console.log("onreadystatechange", ev)
                switch (http.readyState) {
                    case 1://OPENED  ()
                        break;
                    case 2://HEADERS_RECEIVED ()
                        break;
                    case 3://LOADING ()
                        break;
                    case 4://DONE ()
                        if (http.status == 200) {
                            if (onDone) {
                                if (http.responseType == "json" || http.responseType == "text" || http.responseType == "document") {
                                    onDone(http.responseText || http.response, http.status);
                                } else {
                                    onDone(http.response || http.responseText, http.status);
                                }
                            }
                        } else {
                            if (onError) {
                                if (http.responseType == "json" || http.responseType == "text" || http.responseType == "document") {
                                    onError(http.responseText || http.response, http.status);
                                } else {
                                    onError(http.response || http.responseText, http.status);
                                }
                            }
                        }
                        clearListener();
                        break;
                }
            }
            http.onprogress = (ev) => {
                // console.log("onprogress", ev)
                if (onProgress) {
                    onProgress(ev.loaded, ev.total)
                }
            }
            http.onabort = (ev) => {
                // console.log("onabort", ev)
                if (onError) {
                    onError(ev);
                }
                clearListener();
            }
            http.onerror = (ev) => {
                // console.log("onerror", ev)
                if (onError) {
                    onError(ev);
                }
                clearListener();
            }
            http.ontimeout = (ev) => {
                // console.log("ontimeout", ev)
                if (onTimeout) {
                    onTimeout();
                }
                clearListener();
            }

            if (http.upload && onUploadProgress) {
                http.upload.onprogress = (ev) => {
                    onUploadProgress(ev.loaded, ev.total)
                }
            }

            if (headMap) {
                for (let key in headMap) {
                    let value = headMap[key]
                    http.setRequestHeader(key, value);
                }
            }
            http.send(data);

        }

    }
}