namespace slib {

    let SDKLocTable = {}
    SDKLocTable["en"] = {
        "welcome": "Welcome",
        "remind_tip": "Friendly Tips",
        "bind_fail": "Binding account fail",
        "bind_succ": "Binding account success",
        "ok": "OK",
        "cancel": "Cancel",

    }

    SDKLocTable["zh"] = {
        "welcome": "",
        "remind_tip": "",
        "bind_fail": "",
        "bind_succ": "",
        "ok": "",
        "cancel": "",
    }

    SDKLocTable["zh-hk"] = {
        "welcome": "",
        "remind_tip": "",
        "bind_fail": "",
        "bind_succ": "",
        "ok": "",
        "cancel": "",
    }

    export class I18N {

        static readonly instance = new I18N()

        /**
         * 
         */
        language: string = "zh"

        /**
         * 
         */
        stringsMap: { [key: string]: string } = {}

        /**
         * 
         */
        resRootPath: string = ""


        /**
         * 
         */
        dynChange: boolean = false

        get langMap() {
            return this.stringsMap;
        }

        /**
         * 
         * @param data   key value  map
         */
        applyTable(data: { key: string, value: string }[] | { [key: string]: string }) {
            this.stringsMap = {}
            if (data instanceof Array) {
                for (let v of data) {
                    this.stringsMap[v.key] = v.value
                }
            } else {
                for (let k in data) {
                    this.stringsMap[k] = data[k]
                }
            }
        }

        /**
         * key
         * @param key 
         * @param defaultValue 
         */
        locConfig(configRow: any, key: string): string | Function {
            if (this.dynChange) {
                let retFunc = () => {
                    return configRow[key]
                }

                retFunc.toString = () => {
                    return retFunc()
                }
                retFunc["replace"] = (searchValue, replaceValue) => {
                    let retStr = retFunc()
                    return retStr.replace(searchValue, replaceValue)
                }

                retFunc["search"] = (searcher) => {
                    let retStr = retFunc()
                    return retStr.search(searcher)
                }

                retFunc["split"] = (seperator, limit?) => {
                    let retStr = retFunc() as string
                    return retStr.split(seperator, limit)
                }
                return retFunc
            } else {
                return configRow[key]
            }

        }

        /**
         * key
         * @param key 
         * @param defaultValue 
         */
        locString(key: string, defaultValue?: string): string | Function {
            if (this.dynChange) {
                let retFunc = () => {
                    if (this.stringsMap[key]) {
                        return this.stringsMap[key];
                    }
                    else {
                        return defaultValue || key;
                    }
                }

                retFunc.toString = () => {
                    return retFunc()
                }
                retFunc["replace"] = (searchValue, replaceValue) => {
                    let retStr = retFunc()
                    return retStr.replace(searchValue, replaceValue)
                }

                retFunc["search"] = (searcher) => {
                    let retStr = retFunc()
                    return retStr.search(searcher)
                }

                retFunc["split"] = (seperator, limit?) => {
                    let retStr = retFunc() as string
                    return retStr.split(seperator, limit)
                }
                return retFunc
            } else {
                if (this.stringsMap[key]) {
                    return this.stringsMap[key]
                } else {
                    return defaultValue || key
                }
            }

        }

        /**
         * 
         * ###
         * * key="pet.get"
         * * value="${petName},"
         * * format("pet.get","${petName},",{petName:""})  ","
         * @param key key
         * @param defaultValueOrArgs 
         * @param args 
         */
        format(key: string, defaultValueOrArgs: string | { [key: string]: any }, argsMap?: {
            [key: string]: any
        }) {
            if (this.dynChange) {
                let retFunc = () => {
                    let defaultValue = argsMap == null ? undefined : defaultValueOrArgs as string;
                    let args = argsMap == null ? defaultValueOrArgs : argsMap;
                    // ${} 
                    var valueFunc = this.locString(key, defaultValue) as Function;
                    let value = valueFunc()
                    var list = value.match(/\${[a-zA-Z0-9]+}/g);
                    for (var _i = 0, list_1 = list; _i < list_1.length; _i++) {
                        var m = list_1[_i];
                        var key_1 = m.replace(/\$|{|}/g, "");
                        value = value.replace(m, String(args[key_1]));
                    }
                    return value;
                }
                retFunc.toString = () => {
                    return retFunc()
                }

                retFunc["replace"] = (searchValue, replaceValue) => {
                    let retStr = retFunc()
                    return retStr.replace(searchValue, replaceValue)
                }

                retFunc["search"] = (searcher) => {
                    let retStr = retFunc()
                    return retStr.search(searcher)
                }

                retFunc["split"] = (seperator, limit?) => {
                    let retStr = retFunc() as string
                    return retStr.split(seperator, limit)
                }
                return retFunc
            } else {
                let defaultValue = argsMap == null ? undefined : defaultValueOrArgs as string;
                let args = argsMap == null ? defaultValueOrArgs : argsMap;
                // ${} 
                let value = this.locString(key, defaultValue) as string;
                let list = value.match(/\${[a-zA-Z0-9]+}/g)
                for (let m of list) {
                    let key = m.replace(/\$|{|}/g, "")
                    value = value.replace(m, String(args[key]));
                }
                return value;
            }

        }

        /**
         * url resRootPath+value
         * @param url 
         */
        locUrl(url: string): string {
            let rootPath = this.resRootPath.replace(/\/+$/g, "");///
            let path = url.replace(new RegExp(rootPath + "/[a-zA-Z0-9-_]+/"), rootPath + "/" + this.language + "/")
            return path
            //let url = slib.i18n.locString(key);
            //return (this.resRootPath + "/" + this.language + "/" + url).replace(/\/+/g, "/");///
        }

        /**
         * sdk local
         * @param url 
         */
        locSDKString(key: string, lang?: string): string {
            lang = lang ? lang : this.language
            if (SDKLocTable[lang] == null) {
                lang = "zh" // to default
            }
            let ret = SDKLocTable[lang][key]
            return ret ? ret : ""
        }

    }

    export var i18n = I18N.instance
}