namespace slib {
    /**
     * * 
     * * 
     */
    export class SeedRandom {
        min: number = 0
        max: number = 1000000
        seed: number;//
        constructor(startSeed) {
            this.seed = startSeed
        }

        /**
         *  0~1
         */
        next(): number {
            this.seed = (this.seed * 9301 + 49297) % 233280;
            return this.seed / 233280.0;
        }
    }
}