namespace slib.JSHelper {

	/**
	 * @noSelf
	 */
	export function clone<T extends Object>(srcObj: T): T {
		slib.assert(typeof srcObj == 'object', 'srcObj is not a table for clone')
		let destObj = <T>{}
		for (let key in srcObj) {
			if (key != undefined) {
				destObj[key] = srcObj[key]
			}
		}
		return destObj
	}

	/**
	 * @noSelf
	 */
	export function merge<T extends Object>(srcObj: Object, destObj: T): T {
		slib.assert(typeof srcObj == 'object', 'srcObj is not a table for merge')
		slib.assert(typeof destObj == 'object', 'destObj is not a table for merge')

		for (let key in srcObj) {
			if (key != undefined) {
				destObj[key] = srcObj[key]
			}
		}

		return destObj
	}

	/**
	 * @noSelf
	 */
	export function repack(a: any) {
		return window["repack$-"][a];
	}
}
