namespace slib.EncryptHelper {
	/**
	 * 
	 */
	export var enableZip: boolean = false

	/**
	 * @noSelf
	 */
	export function decrypt(value: string, key: string) {
		let info: any = {
			version: "1"
		}
		let endPos = -1
		if (value && value.indexOf('${') == 0 && (endPos = value.indexOf('}^')) > -1) {
			try {
				info = JSON.parse(value.substr(2, endPos - 2))
				value = value.substr(endPos + 2)
			} catch (e) {
				console.error('JSON.parse string failed, rawdata: ', value)
				throw e
			}
		}
		let result: string
		if ("1" < info.version && info.version < "3") {
			let bytes = slib.xxtea.decrypt(value, key)
			result = pako.inflate(bytes, { to: 'string' })
		} else {
			result = slib.xxtea.decryptFromBase64Ex1(value, key)
		}

		//\u0000
		let oldLength = result.length
		result = result.replace(/[\u0000-\u001f]/g, "")//0~31

		if (oldLength != result.length) {
			console.error("0-31");
		}

		return result;
	}

	/**
	 * @noSelf
	 */
	export function encrypt(value: string, key: string, extdata: any = null) {

		if (enableZip && value) {
			let info: any = {
				version: '2'
			}

			if (extdata) {
				for (let k in extdata) {
					info[k] = extdata[k]
				}
			}

			const rawvalue = value
			const deflateValue = pako.deflate(rawvalue)
			value = '${' + JSON.stringify(info) + '}^' + slib.xxtea.encryptToString(deflateValue, key)
			console.log(`compressed result: ${rawvalue.length} -> ${deflateValue.length} -> ${value.length}`)
		} else {
			value = slib.xxtea.encryptToBase64Ex1(value, key)
		}
		return value
	}
}