namespace slib {

    type LogParam = { time?: boolean, tags?: string[] }

    export class Log {

        private static _enablePlaneLog: boolean = false;
        public static get enablePlaneLog(): boolean {
            return Log._enablePlaneLog;
        }
        public static toPlaneLog(args: any[]) {
            const plainTexts = []
            for (let info of args) {
                let ret = ''
                if (info instanceof Error) {
                    ret = `Error content: ${JSON.stringify(info)}\n${info.stack}`
                } else if (info instanceof Object) {
                    ret = JSON.stringify(info)
                } else {
                    ret = info
                }
                plainTexts.push(ret)
            }

            return plainTexts
        }

        public static set enablePlaneLog(value: boolean) {
            Log._enablePlaneLog = value;
            if (value) {
                const logOld = console.log
                const warnOld = console.warn
                const errorOld = console.error
                console['logOld'] = console['logOld'] || logOld
                console['warnOld'] = console['warnOld'] || warnOld
                console['errorOld'] = console['errorOld'] || errorOld
                console.log = function (...args) {
                    const plainTexts = Log.toPlaneLog(args)
                    logOld.apply(console, plainTexts)
                }

                console.warn = function (...args) {
                    const plainTexts = Log.toPlaneLog(args)
                    warnOld.apply(console, plainTexts)
                }

                console.error = function (...args) {
                    const plainTexts = Log.toPlaneLog(args)
                    errorOld.apply(console, plainTexts)
                }
            } else {
                if (console['logOld']) {
                    console.log = console['logOld']
                }
                if (console['warnOld']) {
                    console.warn = console['warnOld']
                }
                if (console['errorOld']) {
                    console.error = console['errorOld']
                }
            }
        }

        protected static _instance: Log
        public static get instance(): Log {
            if (!this._instance)
                this._instance = new Log();
            return this._instance;
        }

        protected time?: boolean
        protected tags?: string[]

        constructor({ time, tags }: LogParam = {}) {
            this.time = time
            if (tags) {
                this.tags = tags.concat()
            }
        }

        /**
         * 
         */
        info(...args) {
            if (this.tags) {
                args = this.tags.concat(args)
            }
            if (this.time) {
                args.push(new Date().getTime())
            }
            console.log(' ', ...args);
        }

        /**
         * 
         */
        warn(...args) {
            if (this.tags) {
                args = this.tags.concat(args)
            }
            if (this.time) {
                args.push(new Date().getTime())
            }
            console.warn(' ', ...args);
        }

        /**
         * 
         */
        error(...args) {
            if (this.tags) {
                args = this.tags.concat(args)
            }
            if (this.time) {
                args.push(new Date().getTime())
            }
            console.error(' ', ...args);
            for (let p of args) {
                if (p instanceof Error) {
                    console.log(p.stack)
                }
            }
            console.log('>>>error')
            console.log(new Error().stack)
        }

    }

    export var log = Log.instance

}