namespace slib {

    /**
     * 
     */
    type BusinessContentType = "json" | "protobuf"

    /**
     * 
     */
    type BusinessProtocol = "http" | "https" | "ws" | "wss"

    type BusinessRequestData = {
        /**
         * api
         */
        action: string,
        /**
         * 
         */
        data: any,
        /**
         * 
         */
        requestType: string,
        /**
         * 
         */
        responseType: string,
        /**
         * flase
         */
        modal?: boolean,
        /**
         * 
         * @noSelf
         */
        errorCallback?: (error: any, retry: () => void) => void

        /**
         * 
         */
        timeout?: number
    }

    type BusinessRequestingData = BusinessRequestData & {
        /**
         * id
         */
        requestIndex: number,

        /**
         * 
         */
        isRetry: boolean,

        /**
         * 
         */
        retryCount: number
    }

    export class BusinessClient {
        /**
         *   http https ws wss 
         * * http://sb.mosoga.net:8080/
         */
        readonly url: string;
        readonly protocol: BusinessProtocol
        readonly hostname: string
        readonly port: number

        /**
         * 
         */
        version?: string;

        /**
         * 
         */
        readonly contentType: BusinessContentType

        /**
         * token
         * * http https 
         * *  ws  wss 
         */
        readonly token: string

        /**
         * 
         */
        readonly timeout: number

        /**
         * +1
         */
        protected _requestIndex = 0;

        /**
         * 
         */
        protected _protocolContent: string | undefined = undefined;
        /**
         * 
         */
        protected _packageName: string | undefined = undefined;

        /**
         * 
         * @noSelf
         */
        errorCallback?: (error: any, retry: () => void) => void;

        /**
         * 
         * @noSelf
         */
        showLoadingModalCallback?: (index: number, url: string) => void;

        /**
         * 
         * @noSelf
         */
        closeLoadingModalCallback?: (index: number, url: string) => void;


        /**
         * http
         */
        protected _httpClient?: HttpClient;

        constructor(data: {
            /**
             * 
             */
            url: string,
            /**
             * 
             */
            contentType: BusinessContentType,
            /**
             * token
             */
            token: string,

            /**
             * 
             */
            version?: string,

            /**
             * 
             */
            timeout?: number
        }) {
            this.url = data.url;
            this.version = data.version;
            this.contentType = data.contentType;
            this.token = data.token;
            this.timeout = data.timeout || 3000;

            var matching = /([a-zA-Z]+):\/\/([\w-.]+):?(\d+)?/.exec(this.url.toLocaleLowerCase());
            if (matching == null) {
                throw new Error(`${this.url}`);
            }
            var [_, protocol, hostname, port] = matching;

            if (["http", "https", "ws", "wss"].indexOf(protocol) == -1) {
                throw new Error(`${this.url}`);
            }

            if (port == null) {
                if (protocol == "http") {
                    port = "80";
                } else if (protocol == "https") {
                    port = "443";
                } else if (protocol == "ws") {
                    port = "80";
                } else if (protocol == "wss") {
                    port = "443";
                }
            } else {
                if (parseInt(port).toString() != port) {
                    throw new Error(`${this.url}`);
                }
            }
            this.protocol = protocol as BusinessProtocol;
            this.hostname = hostname;
            this.port = parseInt(port);
        }

        /**
         * 
         */
        loadProtocol(proto: string) {
        }

        /**
         * api
         */
        async request(data: BusinessRequestData) {
            var requesting = data as BusinessRequestingData;
            requesting.requestIndex = this._requestIndex++;
            requesting.isRetry = false;
            requesting.retryCount = 0;
            return await this.requesting(requesting);
        }

        /**
         * 
         */
        protected async requesting(data: BusinessRequestingData) {
            if (this.protocol == "http" || this.protocol == "https") {
                return await this.httpRequesting(data);
            } else {
                return await this.websocketRequesting(data);
            }
        }

        /**
         * 
         * @param data 
         * @param type 
         * @returns 
         */
        protected serializeRequest(data: any, type: string): any {
        }

        /**
         * 
         * @param data 
         * @param type 
         */
        protected deserializationResponse(data: any, type: string): any {
        }

        protected httpRequesting(requestingData: BusinessRequestingData) {
            return new Promise((resolve, reject) => {
                if (this._httpClient == null) {
                    this._httpClient = new HttpClient();
                }

                var url = `${this.protocol}://${this.hostname}:${this.port}/${this.version}/${requestingData.action}`

                var headMap: { [key: string]: string } = {}
                headMap["Content-Type"] = this.contentType == "json" ? "application/json;charset=utf-8" : "application/x-protobuf";
                headMap["Authorization"] = this.token;

                headMap["Request-Index"] = requestingData.requestIndex.toString();
                headMap["Retry-Count"] = requestingData.retryCount.toString();

                console.warn("[BusinessClient][request]", url, JSON.stringify(headMap));

                var requestData = requestingData.data;

                //
                if (!requestingData.isRetry && requestingData.modal && this.showLoadingModalCallback) {
                    this.showLoadingModalCallback(requestingData.requestIndex, url);
                }

                var onError = (error: any, code?: number) => {
                    //
                    if (requestingData.modal && this.closeLoadingModalCallback) {
                        this.closeLoadingModalCallback(requestingData.requestIndex, url);
                    }

                    if (code == 202) {
                        //
                        let r = this.deserializationResponse(error, "BusinessErrorVO");
                        console.error("[BusinessClient][response]", code, url, JSON.stringify(headMap), JSON.stringify(r));
                        reject({ type: "business-error", error: r })
                    } else if (code == 500) {
                        //
                        let r = this.deserializationResponse(error, "InternalServerErrorVO");
                        console.error("[BusinessClient][response]", code, url, JSON.stringify(headMap), JSON.stringify(r));
                        reject({ type: "internal-server-error", error: r })
                    } else {
                        console.error("[BusinessClient][response]", code, url, JSON.stringify(headMap), error);
                    }

                    //
                    let retry = () => {
                        //
                        if (requestingData.modal && this.showLoadingModalCallback) {
                            this.showLoadingModalCallback(requestingData.requestIndex, url);
                        }
                        requestingData.isRetry = true;
                        requestingData.retryCount++;
                        this.httpRequesting(requestingData);
                    }
                    if (requestingData.errorCallback) {
                        //
                        requestingData.errorCallback(error, retry);
                    } else if (this.errorCallback) {
                        //
                        this.errorCallback(error, retry);
                    } else {
                        //
                    }
                    if (typeof error == "string" && error.indexOf('ssl hand shake error') != -1 || error.indexOf('') != -1) {
                        console.error("[BusinessClient][response] ssl ", url);
                    }
                }

                var onDone = (responseData: any) => {
                    //
                    var result = this.deserializationResponse(responseData, requestingData.responseType);
                    console.warn("[BusinessClient][response]", url, JSON.stringify(headMap), JSON.stringify(result));
                    //
                    if (requestingData.modal && this.closeLoadingModalCallback) {
                        this.closeLoadingModalCallback(requestingData.requestIndex, url);
                    }
                    resolve(result);
                }

                this._httpClient.request({
                    method: "POST",
                    url: url,
                    data: this.serializeRequest(requestData, requestingData.requestType),
                    headMap: headMap,
                    onDone: onDone,
                    onError: onError,
                    onTimeout: () => {
                        //
                        onError("timeout");
                    }
                })
            })
        }

        protected websocketRequesting(data: BusinessRequestingData) {

        }

    }
}
