namespace slib {

    type HttpResponseData = {
        succeed: boolean,
        code: 0 | number,
        message: "success" | string,
        ts: number,
        map_min_v: { min: string, max: string },
        errorcode: string,
        data: any
    }
    type HttpRequestData = { appId: any, role: any, index: number, retry: number, data: any }

    let log: Log = new Log({ tags: ["HttpClient"] })
    export class HttpGameClient extends GameClient {
        protected _client: HttpClient = new HttpClient()
        protected _requestIndex = 0;
        protected _retryCount: number = 3;
        protected _timeout: number = 5000;

        public searchExt: () => string;
        public debugURL: string = null
        constructor() {
            super()
        }

        /**
         * 
         * @noSelf
         */
        public static timeStatisticsCallback: (name: string, millisecond: number) => void;

        connect() {
            //http 
        }

        /**
         * 
         */
        public getLogTest: () => boolean = null;

        /**
         * token
         */
        public getToken: () => string = null;

        /**
         * gametoken
         */
        public getGameToken: () => string = null;

        /**
         * openId
         */
        public getOpenId: () => string = null;

        /**
         * roleId
         */
        public getRoleId: () => string = null;

        /**
         * appid
         */
        public getAppId: () => string = () => {
            throw new Error("APPID")
        };

        protected _loadingIndex: number = 0
        /**
         * loading
         */
        public getLoadingIndex: () => number = () => {
            return this._loadingIndex++;
        };

        public set timeout(value: number) {
            this._timeout = value;
        }


        /**
         * 
         * @param action url
         * @param data 
         * @param callback 
         * ### 
         * * version 
         * * tag tag
         * * name 
         * * modal 
         * * downloadProgress 
         * * uploadProgress 
         * * errorCallback  retry
         */
        request(action, data, callback: (data: HttpResponseData) => void,
            { version, tag, name, modal, downloadProgress, uploadProgress, errorCallback, customUrl }: {
                version?: number,
                tag?: string,
                name?: string,
                modal?: boolean,
                downloadProgress?: (loaded: number, total: number) => void,
                uploadProgress?: (loaded: number, total: number) => void,
                errorCallback?: (error: any, retry: () => void) => void,
                customUrl?: string,
            } = {}
        ) {
            this._requestIndex++;//id
            var requestData = {
                appId: this.getAppId ? this.getAppId() : undefined,
                role: this.getRoleId ? this.getRoleId() : undefined,//
                // token:this._token,//token
                index: this._requestIndex,//
                retry: 0,//
                v: version,
                t: tag,
                name: name,
                data: data
            }
            if (window["gdkjsb"] && window["gdkjsb"].isCustomBackendCfg && window["gdkjsb"].packageTag) {
                requestData["packageCfgKey"] = window["gdkjsb"].packageTag
            }

            this.requestFromData(action, requestData, callback, { modal, downloadProgress, uploadProgress, errorCallback, customUrl });
        }

        protected requestFromData(action, requestData: HttpRequestData, callback: (data: HttpResponseData) => void,
            { modal, downloadProgress, uploadProgress, errorCallback, customUrl }: { modal?: boolean, downloadProgress?: (loaded: number, total: number) => void, uploadProgress?: (loaded: number, total: number) => void, errorCallback?: (error: any, retry: () => void) => void, customUrl?: string } = {},
            isRetry = false, modalIndex = -1
        ) {

            let data = JSON.stringify(requestData)
            if (typeof (data) == 'string' && data.length > 1200) {
                log.warn("request", action, ':', [data]);
            } else {
                log.warn("request", action, data);
            }

            let search = this.searchExt ? this.searchExt() : "";

            let url: string
            if (this._protocol == "https" && this._port == 443) {
                url = `${this._protocol}://${this._host}/${action}${search}`
            } else if (this._protocol == "http" && this._port == 80) {
                url = `${this._protocol}://${this._host}/${action}${search}`
            } else {
                url = `${this._protocol}://${this._host}:${this._port}/${action}${search}`
            }
            if (this.debugURL) {
                url = `${this.debugURL}/${action}${search}`
            }
            if (customUrl) {
                url = `${customUrl}/${action}${search}`
            }


            let index = modalIndex == -1 ? this.getLoadingIndex() : modalIndex
            //
            if (!isRetry && modal && this._showLoadingModalCallback) {
                this._showLoadingModalCallback(index, action);
            }

            let token = this.getToken ? this.getToken() : undefined

            let headMap: any = {}
            headMap["Content-Type"] = "application/json;charset=utf-8"
            if (token) {
                headMap["token"] = token
            }
            if (this.getRoleId && this.getRoleId()) {
                headMap["role"] = this.getRoleId()
            }
            if (this.getOpenId && this.getOpenId()) {
                headMap["openId"] = this.getOpenId()
            }
            let httpReferer = "http://glee.com/info?"
            if (this.getAppId && this.getAppId()) {
                headMap["appId"] = this.getAppId()
                httpReferer = httpReferer + "appId=" + this.getAppId()
            }

            if (this.getGameToken && this.getGameToken()) {
                headMap["Authorization"] = "Bearer " + this.getGameToken();

                //
                let sign = data
                if (token != null) {
                    sign = token.substr(token.length / 2) + data
                }
                sign = MD5.hex(sign);
                headMap["sign"] = sign;
                // console.log("sign", sign)
            }

            if (this.getLogTest && this.getLogTest()) {
                headMap["logTest"] = true;
            }
            headMap["uiLanguage"] = slib.i18n.language

            // country language
            if (window["gdkjsb"] && window["gdkjsb"].language && window["gdkjsb"].country) {
                headMap["language"] = window["gdkjsb"].language
                headMap["country"] = window["gdkjsb"].country
                if (window["gdkjsb"].packageTag) {
                    httpReferer = httpReferer + "&packageTag=" + window["gdkjsb"].packageTag
                }

                if (window["gdkjsb"].versionCode) {
                    httpReferer = httpReferer + "&versionCode=" + window["gdkjsb"].versionCode
                }
                headMap["Referer"] = httpReferer
            }

            var startTime = Date.now();
            this._client.request({
                method: "POST",
                url: url,
                data: data,
                headMap: headMap,
                timeout: this._timeout,
                onDone: (data) => {
                    //
                    if (typeof (data) == 'string' && data.length > 1200) {
                        log.warn("response", url, action, ':', [data])
                    } else {
                        log.warn("response", url, action, data);
                    }

                    let newData = JSON.parse(data);
                    if (newData.ok != undefined) {
                        callback({
                            succeed: newData.ok,
                            code: newData.c,
                            message: newData.m,
                            ts: newData.ts,
                            map_min_v: newData.map_min_v,
                            errorcode: newData.errorcode,
                            data: newData.r
                        });
                    } else {
                        callback(newData);
                    }

                    if (modal && this._closeLoadingModalCallback) {
                        this._closeLoadingModalCallback(index, action);
                    }

                    if (HttpGameClient.timeStatisticsCallback != null) {
                        HttpGameClient.timeStatisticsCallback(action, Date.now() - startTime);
                    }
                },
                onError: (error) => {

                    let retry = () => {
                        //
                        if (modal) {
                            this._showLoadingModalCallback(index, action);
                        }
                        requestData.retry++
                        this.requestFromData(action, requestData, callback, { modal: modal, downloadProgress: downloadProgress, uploadProgress: uploadProgress, errorCallback: errorCallback, customUrl }, true, index);
                    }

                    if (requestData.retry > this._retryCount) {
                        //

                        if (errorCallback) {
                            errorCallback(error, retry);//
                        } else if (this._errorCallback) {
                            this._errorCallback(error, retry)
                        }
                        if (modal && this._closeLoadingModalCallback) {
                            this._closeLoadingModalCallback(index, action);
                        }
                        log.error(": " + url);
                    } else {
                        requestData.retry++
                        this.requestFromData(action, requestData, callback, { modal: modal, downloadProgress: downloadProgress, uploadProgress: uploadProgress, errorCallback: errorCallback, customUrl }, true, index);
                    }
                    log.error(error);
                    if (error && requestData.retry > this._retryCount && ((error as string).indexOf('ssl hand shake error') != -1 || (error as string).indexOf('') != -1)) {
                        if (this._sslHandShakeErrorCallBack) {
                            this._sslHandShakeErrorCallBack(index, action)
                        }
                    }
                },
                onTimeout: () => {
                    //

                    let retry = () => {
                        //
                        if (modal) {
                            this._showLoadingModalCallback(index, action);
                        }
                        requestData.retry++
                        this.requestFromData(action, requestData, callback, { modal: modal, downloadProgress: downloadProgress, uploadProgress: uploadProgress, customUrl }, true, index);
                    }

                    if (requestData.retry > this._retryCount) {
                        //
                        if (errorCallback) {
                            errorCallback("timeout", retry);//
                        } else if (this._errorCallback) {
                            this._errorCallback("timeout", retry)
                        }
                        if (modal && this._closeLoadingModalCallback) {
                            this._closeLoadingModalCallback(index, action);
                        }
                        log.error(": " + url);
                    } else {
                        requestData.retry++
                        this.requestFromData(action, requestData, callback, { modal: modal, downloadProgress: downloadProgress, uploadProgress: uploadProgress, customUrl }, true, index);
                    }
                },
                onProgress: (loaded: number, total: number) => {
                    if (downloadProgress) {
                        downloadProgress(loaded, total)
                    }
                },
                onUploadProgress: (loaded: number, total: number) => {
                    if (uploadProgress) {
                        uploadProgress(loaded, total)
                    }
                }
            })
        }

        get retryCount() {
            return this._retryCount
        }
        set retryCount(value) {
            this._retryCount = value
        }

        get client() {
            return this._client;
        }
    }
}