
namespace slib {

    const ox8000 = 0x8000
    // ------------------ B ------------------
    function bshift23encodeV2(s) {
        let s1t = []
        for (let i = 0; i < s.length; i++) {
            const code = s.charCodeAt(i)
            if (code < ox8000) {
                s1t.push(String.fromCharCode((code + 23) % ox8000))
                // console.log('code1:', code)
            } else {
                s1t.push(String.fromCharCode(ox8000))
                s1t.push(String.fromCharCode((code + 23 - ox8000) % ox8000))
                // console.log("code:", code)
            }
        }
        return s1t.join("")
    }

    function bshift23decodeV2(s) {
        let s1t = []
        for (let i = 0; i < s.length; i++) {
            const code1 = s.charCodeAt(i)
            if (code1 < ox8000) {
                const value = (code1 - 23 + ox8000) % ox8000
                s1t.push(String.fromCharCode(value))
                // console.log('decode1:', value)
            } else {
                i++
                const code2 = s.charCodeAt(i)
                const value = (code2 - 23 + ox8000) % ox8000 + code1
                s1t.push(String.fromCharCode(value))
                // console.log('decode:', value)
            }
        }
        return s1t.join("")
    }

    // ------------------ B ------------------
    function bshift23encode(s) {
        let s1t = []
        for (let c of s) {
            s1t.push(String.fromCharCode((c.charCodeAt() + 23) % 65536))
        }
        return s1t.join("")
    }

    function bshift23decode(s) {
        let s1t = []
        for (let c of s) {
            s1t.push(String.fromCharCode((c.charCodeAt() - 23) % 65536))
        }
        return s1t.join("")
    }

    function b64decode(s) {
        s = s.split("").reverse().join("")
        return Base64.decode(s)
    }

    function encode(value, log = false) {
        let type = typeof value
        if (type == "number") {
            //
            if (value % 1 === 0) {
                return "a" + bshift23encode(value.toString(16))
            } else {
                return "A" + bshift23encode(value.toString())
            }

        } else if (type == "string") {
            //
            return "S" + bshift23encodeV2(value);
        } else if (type == "boolean") {
            //
            return value ? "f" : "t"
        } else {
            return value
        }
    }

    function decode(value, log = false) {
        let type = typeof value
        if (type == "string") {
            let type = value[0]
            value = value.substr(1);
            if (type == "A") {
                return parseFloat(bshift23decode(value))
            } else if (type == "a") {
                //
                return parseInt(bshift23decode(value), 16)
            } else if (type == "R") {
                //
                return bshift23decode(value);
            } else if (type == "S") {
                //
                return bshift23decodeV2(value);
            } else if (type == "f") {
                return true;
            } else if (type == "t") {
                return false;
            } else if (type == "N") {
                return parseFloat(b64decode(value))
            } else if (type == "n") {
                //
                let [left, right] = value.split("I")
                left = ~-parseInt(left, 36)
                let rr = ~-parseInt(right, 36)
                right = rr / Math.pow(10, rr.toString().length)
                return left >= 0 ? left + right : left - right
            } else if (type == "s") {
                //
                return b64decode(value);
            }
        } else {
            return value
        }
    }

    // ------------------------ B ----------------------

    window["encode"] = encode
    window["decode"] = decode

    let _userId: number | undefined
    let _dynamicKey: number | undefined

    function build(target, property) {
        let value = target[property]
        let type = typeof value

        delete target[property]
        Object.defineProperty(target, property, {
            get: function () {
                return decode(dynamicDecode(this["__" + property + "__"], property))
            },
            set: function (value) {
                this["__" + property + "__"] = dynamicEncode(encode(value), property)
            }
        });
    }

    /**
     * Key
     */
    export class DataFactory {
        static readonly instance: DataFactory = new DataFactory;
        protected _creatorMap: { [key: string]: () => void } = {};
        protected _creatorList: { className: string, creator: () => Object }[] = [];
        constructor() {

        }
        addCreator(className: string, creator: () => Object) {
            this._creatorMap[className] = creator;
            this._creatorList.push({ className: className, creator: creator });
        }

        create(className): Object {
            let creator = this._creatorMap[className];
            if (creator) {
                return new creator();
            }
            return null;
        }
        get creatorList() {
            return this._creatorList;
        }
    }

    /**@noSelf */
    export function setDynamicDecodeKey(key?: number, userId?: number) {
        _userId = userId
        _dynamicKey = key
    }

    /**@noSelf */
    export function dynamicDecode(value: any, valueName: string) {
        if (_dynamicKey && _userId) {
            if (typeof value === "string") {
                let ret = []
                let pa = valueName.charCodeAt(valueName.length - 1) & 23
                let pb = (_userId + _dynamicKey) & 17
                for (let i = 0; i < value.length / 2; i++) {
                    ret.push(String.fromCharCode((value[i * 2 + 1].charCodeAt(0) - ((value[i * 2].charCodeAt(0) >> 2) & 31) - pa - pb)))
                }
                return ret.join("")
            } else if (value instanceof Array) {
                for (let i = 0; i < value.length; i++) {
                    value[i] = decode(dynamicDecode(value[i], valueName + i))
                }
                return value
            } else {
                return value
            }
        } else {
            return value
        }
    }

    /**@noSelf */
    export function dynamicEncode(value: any, valueName: string, userId?: number, dynamicKey?: number) {
        userId = userId || _userId
        dynamicKey = dynamicKey || _dynamicKey
        if (userId && dynamicKey) {
            if (typeof value === "string") {
                let ret = []
                let pa = valueName.charCodeAt(valueName.length - 1) & 23
                let pb = (userId + dynamicKey) & 17
                for (let i = 0; i < value.length; i++) {
                    let v = math.random(255)
                    ret.push(String.fromCharCode(v))
                    ret.push(String.fromCharCode((value[i].charCodeAt(0) + ((v >> 2) & 31) + pa + pb)))
                }
                return ret.join("")
            } else if (value instanceof Array) {
                for (let i = 0; i < value.length; i++) {
                    value[i] = dynamicEncode(encode(value[i]), valueName + i, userId, dynamicKey)
                }
                return value
            } else {
                return value
            }
        } else {
            return value
        }
    }

    /**
     * 
     * @param className  
     * @noSelf
     */
    export function SafeClass(className: string): any {
        return function (ctor: Function) {
            var safeclass: any = function (...args) {
                try {
                    ctor.apply(this, args)
                } catch (e) {
                    console.error(`@SafeClass(${className})(`, args, `)`)
                    throw e
                }

                for (let key in this) {
                    if (this.hasOwnProperty(key)) {
                        let value = this[key]
                        let type = typeof value
                        build(this, key);
                        this[key] = value;
                    }

                }
                this._classname_ = className;

                return this;
            }

            safeclass.prototype = ctor.prototype;
            DataFactory.instance.addCreator(className, safeclass);

            //
            let propertys = Object.getOwnPropertyNames(ctor)
            for (let name of propertys) {
                if (["length", "name", "prototype", "arguments"].indexOf(name) != -1)
                    continue;//   ios10
                try {
                    let obj = Object.getOwnPropertyDescriptor(ctor, name)
                    Object.defineProperty(safeclass, name, obj);
                } catch (e) {
                    console.error("SafeData copy static Property error", name, e);
                }
            }

            return safeclass;
        }
    }

    /**
     * 
     * @param className 
     */
    export function StorageClass(className: string): any {
        return function (ctor: any) {
            ctor.prototype["_classname_"] = className
            DataFactory.instance.addCreator(className, ctor);
        }
    }

    /**
     * 
     * @param target 
     * @param property 
     * @noSelf
     */
    export function SafeProperty(target: object, property: string) {
        let value = target[property]
        build(target, property)
        target[property] = value;
    }

    /**
     * 
     * @param objf 
     * @param fieldMap 
     * @param ekey key
     * @param datastr 
     * @param unzip 
     */
    export function decryptTable(objf: () => object, fieldMap: string[], ekey: string, datastr: string, unzip: boolean = false): any[] {

        let str = null
        if (unzip) {
            let bytes = slib.xxtea.decrypt(datastr, ekey)
            str = pako.inflate(bytes, { to: 'string' })
        } else {
            str = xxtea.decryptFromBase64(datastr, ekey)
        }

        let data = JSON.parse(str)
        let config: any[] = []
        for (let record of data) {
            let obj = objf()
            for (let i = 0; i < fieldMap.length; i++) {
                let key = fieldMap[i]
                obj[key] = record[i]
            }
            config.push(obj)
        }
        return config
    }
}